.about-me {
  background-color: #fff;
  padding: 100px 10%;
  border-top-right-radius: 75px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.left-section {
  width: 40%;
  display: flex;
  flex-direction: column;
}

.right-section {
  width: 60%;
  padding-left: 50px;
}

.left-section h2 {
  font-size: 40px;
  font-weight: 700;
  line-height: 52px;
  margin: 0;
}

.left-section p {
  font-size: 16px;
  line-height: 24px;
}

@media screen and (max-width: 768px) {
  .about-me {
    flex-direction: column;
  }
}
